<template>
  <div class='sidebar'>
    <el-menu :default-active='onRoutes' unique-opened router background-color="#313139" text-color="#818A91" active-text-color="#7B8AFD">
      <el-submenu v-for='(item, index) in items' :key='index' style='font-size: 1.3333rem;' :index='item.index' :route="item.type ? {'name': item.index} : {'name': item.index}">
        <template slot="title">
          <i class='icon iconfont' :class='item.icon'></i>
          <span>{{ item.title }}</span>
        </template>
        <el-menu-item-group>
          <!-- <template slot="title">分组一</template> -->
          <el-menu-item v-for="(sub, idx) in item.children" :key="index + '-' +idx" :index="sub.index" :route="sub.index">
            <i class='icon iconfont' :class='sub.icon'></i>
            {{sub.title}}
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: 'Sidebar',
  computed: {
    ...mapGetters('common/', ['Route']),
    onRoutes () {
      /* // 添加纪录页面的路由指引
      if (this.Route.name.includes('app')) {
        return 'app'
      } else if (this.Route.name.includes('alarm')) {
        return 'alarm'
      } else if (this.Route.name.includes('resource')) {
        return 'resource'
      } else if (this.Route.name.includes('map')) {
        return 'map'
      } else if (this.Route.name.includes('workflow')) {
        return 'workflow'
      } else if (this.Route.name.includes('discussGroup')) {
        return 'discussGroup'
      } else if (this.Route.name.includes('pig_product')) {
        return 'pig_product'
      } else if (this.Route.name.includes('settings')) {
        return 'settings'
      } else if (this.Route.name.includes('dispose')) {
        return 'dispose'
      } else {
        return '404'
      } */
    }
  },
  data () {
    return {
      items: [
        {
          index: 'monitor',
          title: '监控台',
          icon: 'icon-home',
          children: [{
            index: '/monitor/data',
            title: '数据模式',
            icon: 'icon-shuju'
          }, {
            index: '/monitor/map',
            title: '地图模式',
            icon: 'icon-ditu'
          }, {
            index: '/monitor/card',
            title: '卡片模式',
            icon: 'icon-card'
          }]
        },
        {
          index: 'device',
          title: '设备管理',
          icon: 'icon-shebei',
          children: [{
            index: '/device/list',
            title: '设备列表',
            icon: 'icon-bianhaoliebiao'
          }, {
            index: '/device/category',
            title: '设备类别',
            icon: 'icon-home'
          }, {
            index: '/device/producer',
            title: '厂家管理',
            icon: 'icon-home'
          }, {
            index: '/device/deploy',
            title: '部署记录',
            icon: 'icon-home'
          }, {
            index: '/device/log',
            title: '设备日志',
            icon: 'icon-home'
          }, {
            index: '/device/coderDecoder',
            title: '设备日志',
            icon: 'icon-home'
          }]
        },
        {
          index: 'event',
          title: '事件管理',
          icon: 'icon-bukongjingbao',
          children: [{
            index: '/event/list',
            title: '事件列表',
            icon: 'icon-event-list'
          }, {
            index: '/event/rules',
            title: '事件规则',
            icon: 'icon-shijianguoduguize'
          }]
        },
        {
          index: 'account',
          title: '平台账户管理',
          icon: 'icon-alarm1',
          children: [{
            index: '/account/pending',
            title: '待处理',
            icon: 'icon-home'
          }, {
            index: '/account/list',
            title: '账户列表',
            icon: 'icon-home'
          }, {
            index: '/account/admin',
            title: '管理员管理',
            icon: 'icon-home'
          }]
        },
        {
          index: 'app',
          title: '应用管理',
          icon: 'icon-app-manager',
          children: [{
            index: '/app/list',
            title: '应用列表',
            icon: 'icon-app-list'
          }, {
            index: '/app/userAdmin',
            title: '应用用户管理',
            icon: 'icon-app-user-admin'
          }]
        },
        {
          index: 'network',
          title: '网络管理',
          icon: 'icon-alarm1',
          children: [{
            index: '/network/config',
            title: '连接配置',
            icon: 'icon-home'
          }, {
            index: '/network/monitor',
            title: '网络监控',
            icon: 'icon-home'
          }]
        },
        {
          index: 'notify',
          title: '通知管理',
          icon: 'icon-alarm1',
          children: [{
            index: '/notify/list',
            title: '通知列表',
            icon: 'icon-home'
          }, {
            index: '/notify/rules',
            title: '通知规则',
            icon: 'icon-home'
          }, {
            index: '/notify/contactList',
            title: '通讯录',
            icon: 'icon-home'
          }]
        },
        {
          index: 'map',
          title: '地图',
          icon: 'icon-weizhi'
        },
        {
          index: 'workflow',
          title: '工作流',
          icon: 'icon-gongzuoliu'
        },
        {
          index: 'discussGroup',
          title: '讨论组',
          icon: 'icon-taolun'
        },
        {
          index: 'chart',
          title: '报表',
          icon: 'icon-baobiao'
        },
        {
          index: 'settings',
          title: '设置',
          icon: 'icon-shebeisheshi',
          children: [{
            index: 'settings/one',
            title: '选项一',
            icon: 'icon-home'
          }, {
            index: 'settings/two',
            title: '选项二',
            icon: 'icon-home'
          }]
        }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
.sidebar{
  height: 100%;
  text-align: left;
  .el-menu {
    border-right: none;
  }
}
.sidebar .iconfont{
  font-size: 22px;
}
.sidebar > ul {
  height: 100%;
}
</style>



