<template>
  <el-container style="height: 100%" class="v-container">
    <el-header>
      <el-row style="overflow: hidden;" type="flex">
        <img :src="logo" alt="logo" title="返回首页" @click="toApp" style="margin-right: 0.5rem; height:4rem;cursor: pointer;">
        <el-menu :default-active="state.path" ref="nav" :router="true" mode="horizontal" background-color="#000000" text-color="#fff">
          <el-menu-item :index="menu.path" v-for="(menu, i) in menus" :key="i" @click="state.path = menu.path">
            <template>
              <span :class="menu.icon" v-if="menu.icon" style="margin-right: 2px; position: relative; top: -1px; font-size: 24px;"></span>{{menu.title}}
            </template>
          </el-menu-item>
        </el-menu>
      </el-row>
      <el-dropdown class="login-info" size="small" trigger="click">
        <span class="el-dropdown-link">
          <span style="color: #fff;">{{username}}</span>
          <i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>
            <a @click="signOut">退出</a>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <el-row :gutter='0' class="aside-content">
      <el-col :span='4' class="side-menu">
        <side-bar></side-bar>
      </el-col>
      <el-col :span='20' class='content'>
        <el-main style="width:0; flex-basis:auto;"></el-main>
        <keep-alive>
          <router-view style="flex-basis: 100%;"></router-view>
        </keep-alive>
      </el-col>
    </el-row>
  </el-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import SideBar from '@/components/Sidebar'

export default {
  name: 'iot-admin',
  components: {
    'side-bar': SideBar
  },
  computed: {
    ...mapGetters({
      userInfo: 'common/userInfo'
    }),
    username: function () {
      if (this.userInfo && this.userInfo.username) {
        return this.userInfo.username
      } else {
        return ''
      }
    },
    logo: function () {
      if (this.username === 'zt') {
        return require('../assets/logo.jpg')
      } else {
        return require('../assets/images/Hilink-logo.png')
      }
    }
  },
  data () {
    return {
      state: this.$store.state.common,
      menus: [
        '概况面板',
        '告警异常'
      ]
    }
  },
  methods: {
    ...mapActions({
      features: 'common/features'
    }),
    update () {
    },
    signOut () {
      // 调用登出接口1
      this.$router.push('/login')
      // 清除权限信息
      this.$store.commit('common/setLoginInfo', {
        userInfo: null
      })
    },
    toApp () {
      // 跳转到首页
      this.$router.push('/monitor')
    }
  },
  created () {
    this.features().then(fs => {
      let menus = []

      let path = this.state.path
      let exist
      for (let feature of fs) {
        if (feature && feature.name) {
          menus.push(feature)
          if (path == feature.path) {
            exist = true
          }
        }
      }
      // TODO 只展示home和login
      menus = menus.filter(m => {
        return m.name === 'login'
      })
      this.menus = menus
      if (!exist) {
        if (menus[0] && menus[0].path) {
          this.state.path = menus[0].path
          this.$router.push(this.state.path)
        }
      }
      this.update()
      window.onresize = this.update
    })
  }
}
</script>

<style lang="less" scoped>
@import '../assets/style/variable.less';
.v-container {
  .el-header,
  .el-main,
  .el-footer{
    padding: 0;
    position: relative;
    overflow: hidden;
  }

  .el-header{
    width: 100%;
    z-index: 100;
    opacity: 0.8;
    height: 48px !important;
    background: #28282e;
    .el-menu-item:focus {
      color: #3bafda !important;
    }
    .el-menu-item.is-active:hover {
      color: #3bafda !important;
    }
    .login-info {
      position: absolute;
      top: 12px;
      right: 2rem;
      cursor: pointer;
    }
  }

  .aside-content {
    height: calc(100% - 4rem);
    .side-menu {
      height:100%;
      overflow-y: auto;
    }
  }

  .content {
    // padding: 20px 20px 100px 20px;
    // border-left: 1px solid rgba(103, 198, 157, .6);
    height: 100%;
    min-height: 400px;
    box-sizing: border-box;
    overflow-y: scroll;
  }
}
</style>



